<template>
  <div id="roomManage">
    <!-- 输入房间名称 -->
    <div class="search_box">
      <form class="flex">
        <img
          src="@/assets/five/search.png"
          class="search_img"
          alt
        />
        <input
          onkeydown="if(event.keyCode==13){event.keyCode=0;event.returnValue=false}"
          class="search_input"
          type="search"
          v-model="servalue"
          placeholder="输入房间名称"
        />
      </form>
    </div>
    <!-- 添加房间 -->
    <van-button
      type="default"
      icon="add"
      class="add_box"
      @click="goNext('/addRooms')"
    >添加房间</van-button>
    <!-- 房间 -->
    <div class="scroll">
      <div
        v-for="(item,i) in roomAll"
        :key="i"
        class="room_box"
        @click="goNext('',item)"
      >
        <div style="width:40%;text-align:left;">
          <div class="flex">
            <div class="font1 mr10">{{ item.roomSn }}</div>
            <img
              v-if="!item.mac"
              style="width:16px;height:16px;"
              src="../../assets/room_model/close.png"
            >
            <img
              v-else
              style="width:16px;height:16px;"
              src="../../assets/room_model/open.png"
            >
          </div>
          <div style="font-size:0.6rem;">
            <span v-if="item.buildNum">{{ item.buildNum }}栋</span>
            <span v-if="item.unit">{{ item.unit }}单元</span>
            <span v-if="item.floor">{{ item.floor }}楼</span>
          </div>
        </div>
        <div
          v-if="scaleObj"
          style="width:40%;"
        >{{ scaleObj[item.roomScaleId] }}
          <van-icon name="arrow-down" />
        </div>
        <div
          class="flex"
          style="width:20%;display:flex;justify-content:right;"
          @click.stop="goNext('/addRooms',item)"
        >
          <div>详情</div>
          <van-icon name="arrow" />
        </div>
      </div>
    </div>
    <!-- 选择基础房型 -->
    <van-popup
      v-model="show_scale"
      position="bottom"
    >
      <van-picker
        show-toolbar
        :columns="scaleAll"
        @confirm="onConfirmScale"
        @cancel="show_scale = false"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import { GetBusinessRoomscaleSimpleByhotel, PostBusinessRoomEdit } from '@/api/api_hotel';
import { GetBusinessRoom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            servalue: null,
            scaleObj: null,
            show_scale: false,
            scaleAll: [],
            roomAll: [],
            dataForm: {
                hotelId: this.$route.query.hotelId,
                roomSn: null,
                isGate: '0'
            }
        };
    },
    watch: {
        servalue() {
            this.dataForm.roomSn = this.servalue;
            this.getCurrentInfo();
        }
    },
    created() {
        this.$store.commit('setHeader', '房间管理');
        if (localStorage.getItem('rooomManage_search')) {
            this.dataForm.roomSn = this.servalue = localStorage.getItem('rooomManage_search');
        }
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            GetBusinessRoom(this.dataForm).done((res) => {
                this.roomAll = res.data;
                this.HotelStateListGet();
            });
        },
        // 获取基础房型信息
        HotelStateListGet() {
            GetBusinessRoomscaleSimpleByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.scaleOldAll = res.data;
                this.scaleAll = res.data.map((item) => item.scaleName);
                let obj = {};
                res.data = res.data.map((item) => {
                    obj[item.id] = item.scaleName;
                });
                this.scaleObj = obj;
            });
        },
        goNext(url, item) {
            if (item) {
                if (url) {
                    this.$router.push({
                        path: url,
                        query: {
                            hotelId: this.$route.query.hotelId,
                            id: item.id
                        }
                    });
                    if (this.dataForm.roomSn) {
                        localStorage.setItem('rooomManage_search', this.dataForm.roomSn);
                    } else {
                        localStorage.removeItem('rooomManage_search');
                    }
                } else {
                    this.show_scale = true;
                    this.current_item = item;
                    // this.HotelStateListGet();
                }
            } else {
                this.$router.push({
                    path: url,
                    query: {
                        hotelId: this.$route.query.hotelId
                    }
                });
            }
        },
        // 选择基础房型
        onConfirmScale(e) {
            this.scaleName = e;
            this.roomScaleId = this.scaleOldAll.find((item) => item.scaleName == e).id;
            this.show_scale = false;
            this.commit();
        },
        // 提交-基础房型
        commit() {
            let data = {
                hotelId: this.current_item.hotelId,
                id: this.current_item.id,
                roomSn: this.current_item.roomSn,
                buildNum: this.current_item.buildNum,
                unit: this.current_item.unit,
                floor: this.current_item.floor,
                hotelLockType: this.current_item.hotelLockType,
                roomLevel: this.current_item.roomLevel,
                lockInstall: this.current_item.lockInstall,
                doorType: this.current_item.doorType,
                roomSort: this.current_item.roomSort,
                landlord: this.current_item.landlord,
                // change
                roomScaleId: this.roomScaleId
            };
            PostBusinessRoomEdit(data).done((res) => {
                Toast(res.msg);
                this.getCurrentInfo();
            });
        }
    }
};
</script>
<style scoped>
#roomManage {
    font-size: 0.8rem;
    color: #000;
}
.search_box {
    font-size: 0.7rem;
    padding: 10px 0;
}
.flex {
    display: flex;
    align-items: center;
}
.search_img {
    width: 22px;
    height: 22px;
    position: absolute;
    top: 68px;
    left: 15px;
}
.search_input {
    margin: 0 auto;
    background: #fff;
    width: 96%;
    text-indent: 3em;
    padding: 12px 0;
    border-radius: 5px;
}
.add_box {
    width: 96%;
    color: #ff7c41;
    border-radius: 5px;
}
.scroll {
    background: rgb(255, 255, 255);
    margin-top: 10px;
    position: fixed;
    top: 150px;
    left: 0;
    right: 0;
    bottom: 10px;
    overflow: auto;
}
.room_box {
    display: flex;
    justify-content: space-between;
    padding: 16px 10px;
    background: #fff;
    align-items: center;
    border-bottom: 2px solid #f8f8f8;
    margin: 0 10px;
    color: #b2b2b2;
    font-size: 0.7rem;
}
.font1 {
    font-size: 1rem;
    color: #000;
    font-weight: bold;
}
.mr10 {
    margin-right: 10px;
}
</style>



